export default {
  nonEmpty(value) {
    if (!value) return new Error('Поле не должно быть пустым');
    return null;
  },
  getByName(name) {
    if (this[name]) {
      return this[name];
    }
    // eslint-disable-next-line no-console
    console.warn(`Валидатор ${name} не определен.`);
    return null;
  },
};
