import $ from 'jquery';
import Sortable from 'sortablejs';

const updateSectionArticlesIndices = (
  section,
  sectionsKey,
  articlesKey,
  sectionIndex
) => {
  const prefix = `${sectionsKey}[${sectionIndex}][${articlesKey}]`;

  $(section)
    .find(`[name^="${prefix}"]`)
    .each((articleIndex, input) => {
      const $input = $(input);
      const name = $input.attr('name');
      const escapedPrefix = prefix.replace(/[\][]/g, '\\$&');
      const regexp = new RegExp(`${escapedPrefix}\\[\\d*?\\]`);
      const newName = name.replace(regexp, `${prefix}[${articleIndex}]`);

      $input.attr('name', newName);
    });
};

const updateSectionItemIndices = (section, sectionsKey, articlesKey, index) => {
  $(section)
    .find(`[name^="${sectionsKey}"]`)
    .each((_, input) => {
      const $input = $(input);
      const name = $input.attr('name');
      const regexp = new RegExp(`${sectionsKey}\\[\\d*?\\]`);
      const newName = name.replace(regexp, `${sectionsKey}[${index}]`);

      $input.attr('name', newName);
    });

  updateSectionArticlesIndices(section, sectionsKey, articlesKey, index);
};

const initializeSectionsList = (
  form,
  {
    selector = '.sections-list-container',
    sectionsKey = 'sections',
    articlesKey = 'articles',
  } = {}
) => {
  const $form = $(form);
  const $container = $(selector);

  if (!$container.length) return;

  const $sectionTemplate = $container.find('template#sections-list-item');
  const $articleTemplate = $container.find(
    'template#section-articles-list-item'
  );

  const $sectionsList = $container.find('.sections-list');
  const $sectionArticleLists = $container.find('.section-articles-list');
  const $newSectionLink = $container.find(
    '.sections-list-header .a__actions-link'
  );

  Sortable.create($sectionsList.get(0), {
    handle: '.sections-list-item-actions > .--icon-drag',
  });

  $sectionArticleLists.each((_, sectionArticleList) => {
    Sortable.create(sectionArticleList, {
      handle: '.--icon-drag',
    });
  });

  $form.on('submit', () => {
    $container.find('.sections-list-item').each((index, section) => {
      updateSectionItemIndices(section, sectionsKey, articlesKey, index);
    });
  });

  // Handle new section creation.
  $newSectionLink.click(event => {
    event.preventDefault();

    $sectionsList.append($sectionTemplate.html());
  });

  // Handle section removal.
  $(document).on(
    'click',
    '.sections-list-item-actions .--icon-remove',
    event => {
      $(event.target)
        .closest('.sections-list-item')
        .remove();
    }
  );

  // Handle article removal.
  $(document).on(
    'click',
    '.section-articles-list-item .--icon-remove',
    event => {
      $(event.target)
        .closest('.section-articles-list-item')
        .remove();
    }
  );

  // Handle article adding.
  $(document).on('input', '.sections-list-item [list]', event => {
    const $input = $(event.target);
    const $datalist = $(event.target.list);
    const $option = $datalist.find(`option[value="${$input.val()}"]`);

    if (!$option.length) return;

    const title = $option.val();
    const { id } = $option.data();
    const $article = $($articleTemplate.html());
    const $sectionArticlesList = $input
      .parent()
      .siblings('.section-articles-list');
    const exists =
      $sectionArticlesList.find(
        `.section-articles-list-item input[value="${id}"]`
      ).length > 0;

    $input.val('');

    if (exists) return;

    $article.find('input').val(id);
    $article.find('span').text(title);

    $sectionArticlesList.append($article);

    Sortable.create($sectionArticlesList.get(0), {
      handle: '.--icon-drag',
    });
  });
};

export default initializeSectionsList;
