import '@fortawesome/fontawesome-free/js/fontawesome';
import '@fortawesome/fontawesome-free/js/solid';

import $ from 'jquery';
import ace from 'ace-builds/src-noconflict/ace';
import 'ace-builds/src-noconflict/theme-tomorrow';
import 'ace-builds/src-noconflict/mode-jade';
import initializeSectionsList from './admin/sections-list';
import validators from './form-validators';

import '../styles/admin.scss';

const showNotification = ({ type, message }) => {
  if (!message) return;

  const messageContainer = document.querySelector('.message-container');
  const messageContent = messageContainer.firstChild;

  // eslint-disable-next-line default-case
  switch (type) {
    case 'success':
      messageContent.classList.add('ids__success-toast');
      messageContent.textContent = message;
      break;
    case 'error':
      messageContent.classList.add('ids__error-toast');
      messageContent.textContent = message;
      break;
  }

  messageContainer.classList.add('--active');
  window.setTimeout(() => {
    messageContent.textContent = '';
    messageContent.classList.remove('ids__success-toast', 'ids__error-toast');
    messageContainer.classList.remove('--active');
  }, 3000);
};

const initializeEditors = form => {
  const editorContainers = form.querySelectorAll('.ace-editor');
  const aceEditors = [];

  if (editorContainers.length > 0) {
    editorContainers.forEach(editorContainer => {
      const $input = $(editorContainer).next("input[type='hidden']");
      const error = $(editorContainer).data('error');

      const aceEditor = ace.edit(editorContainer, {
        value: $input.val() || '',
        theme: 'ace/theme/tomorrow',
        mode: 'ace/mode/jade',
        fontFamily: 'PT Mono',
        fontSize: '16px',
        tabSize: 2,
        useSoftTabs: true,
        enableBasicAutocompletion: false,
        showLineNumbers: true,
        showInvisibles: false,
        wrap: true,
        maxLines: Infinity,
        minLines: 10,
      });

      aceEditor.getSession().on('change', () => {
        $input.val(aceEditor.getSession().getValue());
      });

      if (error && error.msg) {
        aceEditor.getSession().setAnnotations([
          {
            row: error.line - 1,
            column: error.column,
            text: error.msg,
            type: 'error',
          },
        ]);
      }

      aceEditors.push(aceEditor);
    });
    return aceEditors;
  }

  return null;
};

const initializeCheckboxes = form => {
  form.addEventListener('change', ({ target }) => {
    const $input = target;

    if ($input.type === 'checkbox') {
      $input.previousSibling.value = !!$input.checked;
    }
  });
};

const validateForm = form => {
  const fieldsForValidation = Array.from(
    form.querySelectorAll('[data-validator]')
  ).filter(({ dataset }) => validators.getByName(dataset.validator));

  const validationErrors = fieldsForValidation
    .map(field => {
      const { dataset, value } = field;
      const validator = validators.getByName(dataset.validator);
      const error = validator(value);
      return { field, error };
    })
    .filter(({ error }) => !!error);

  validationErrors.forEach(({ field, error }) => {
    const errorMessage = field.parentNode.querySelector('[class*="error"]');
    field.classList.add('error-state');
    errorMessage.textContent = error.message;
  });

  return validationErrors.length === 0;
};

const initializeForms = forms => {
  forms.forEach(form => {
    const aceEditors = initializeEditors(form);
    const fieldsForValidation = form.querySelectorAll('[data-validator]');

    initializeCheckboxes(form);
    initializeSectionsList(form);

    // remove validation error on any keypress
    fieldsForValidation.forEach(field =>
      field.addEventListener('keyup', () => {
        const errorMessage = field.parentNode.querySelector('[class*="error"]');
        field.classList.remove('error-state');
        errorMessage.textContent = null;
      })
    );

    form.addEventListener('submit', async event => {
      event.preventDefault();

      if (!validateForm(form)) return;

      const $target = $(event.target);
      const { action, method } = event.target;

      try {
        const data = await $.ajax({
          method,
          url: action,
          data: $target.serialize(),
          dataType: 'json',
        });

        if (data.redirectUrl) {
          window.location = data.redirectUrl;
        } else {
          showNotification({ type: 'success', message: data.message });
          if (aceEditors) {
            aceEditors.forEach(aceEditor =>
              aceEditor.getSession().clearAnnotations()
            );
          }
        }
      } catch (response) {
        const error = response.responseJSON || {};

        form.classList.remove('validation-error');
        void form.offsetWidth;
        form.classList.add('validation-error');

        if (error) {
          showNotification({ type: 'error', message: error.message });

          // If errors while compiling pug then show errors in editor
          if (error.name === 'PugError' && aceEditors) {
            aceEditors.forEach(aceEditor => {
              aceEditor.getSession().setAnnotations([
                {
                  row: error.data.line - 1,
                  column: error.data.column,
                  text: error.data.msg,
                  type: 'error',
                },
              ]);
            });
          }
        }
      }
    });
  });
};

const initializeTable = table => {
  if (table) {
    const deleteLinks = table.querySelectorAll('a[data-method="DELETE"]');

    [...deleteLinks].forEach(deleteLink => {
      const { method, action } = deleteLink.dataset;

      deleteLink.addEventListener('click', async event => {
        event.preventDefault();

        const response = await fetch(action, {
          method,
          headers: {
            'Content-Type': 'application/json',
          },
        });

        if (response.ok) {
          event.target.closest('tr').remove();
        } else {
          const data = await response.json();

          if (data.error) {
            showNotification({ type: 'error', message: data.error.message });
          }
        }
      });
    });
  }
};

const pollDeployJobStatus = $forms => {
  const $form = $forms[0];

  if (!$form) return;

  const $button = $form.querySelector('.deploy-job-status-button');
  const $status = $form.querySelector('.deploy-job-status');

  if (!$button || !$status) return;

  const endpoint = $button.dataset.action;
  const interval = 1000;
  let prevStatus = 'initialized';

  const setSucceededStatus = message => {
    $status.classList.add('ids__form-success-message');
    $status.classList.remove('ids__form-error-message');
    $status.innerHTML = message;
    $button.disabled = false;
  };

  const setProgressStatus = message => {
    $status.classList.remove('ids__form-success-message');
    $status.classList.remove('ids__form-error-message');
    $status.innerHTML = `<i class="fa fa-spinner fa-spin"></i> ${message}`;
    $button.disabled = true;
  };

  const setFailedStatus = (hint, message) => {
    $status.classList.remove('ids__form-success-message');
    $status.classList.add('ids__form-error-message');
    $status.innerHTML = `${hint} ${message || 'Неизвестная ошибка'}`;
    $button.disabled = false;
  };

  const poll = () => {
    $.get(endpoint)
      .then(response => {
        const { status, error } = response || {};
        const hint = $button.dataset[status] || '';

        switch (status) {
          case 'initialized':
          case 'succeeded':
            setSucceededStatus(hint);
            break;
          case 'building':
          case 'deploying':
            if (prevStatus !== status) setProgressStatus(hint);
            setTimeout(poll, interval);
            break;
          case 'failed':
            setFailedStatus(hint, error);
            break;
          default:
        }

        prevStatus = status;
      })
      .catch(error => {
        const hint = $button.dataset.failed || '';
        setFailedStatus(hint, error.message);
      });
  };

  $form.addEventListener('submit', () => {
    $button.disabled = true;
    poll();
  });

  poll();
};

document.addEventListener('keydown', event => {
  if ((event.ctrlKey || event.metaKey) && event.code === 'KeyS') {
    event.preventDefault();

    const submitButton = document.querySelector('button[type=submit]');
    if (submitButton) {
      submitButton.click();
    }
  }
});

document.addEventListener('DOMContentLoaded', () => {
  const table = document.querySelector('table');
  const forms = document.querySelectorAll('form');
  const pageTitle = document.querySelector('h2');
  const titleInput = document.querySelector('#title');

  initializeTable(table);
  initializeForms(forms);
  pollDeployJobStatus(forms);

  // Change title text dynamically
  if (pageTitle && titleInput) {
    let title = titleInput.value;

    titleInput.addEventListener('keyup', event => {
      title = event.target.value;

      if (title) {
        pageTitle.textContent = title;
      } else {
        pageTitle.textContent = '';
      }
    });
  }
});
